/* ==========================================================================
   Product List - Carousel shortcode styles - begin
   ========================================================================== */

.qode-plc-holder {
    @include qodeRelativeHolderLayout();

    .qode-plc-outer {
        @include qodeRelativeHolderLayout();
    }

    .qode-plc-item {
        position: relative;
        display: inline-block;
        vertical-align: top;
        width: 100%;
        padding: 0;
        z-index: 100;
        overflow: hidden;
        box-sizing: border-box;

        .touch & {
            cursor: pointer;
        }

        .qode-plc-title {
            margin: 0 0 8px;
            color: #000;
            letter-spacing: 0;
            text-transform: none;
        }

        .qode-plc-category {
            margin: 0 0 4px;
            a{
                text-transform: uppercase;
                color: #888;
                font-size: 11px;
                font-weight: 500;
                letter-spacing: 1px;
            }

        }

        .qode-plc-excerpt {
            color: #000;
            line-height: 1.3em;
            margin: 0px;
        }

        .qode-plc-rating-holder {
            @include qodeRelativeHolderLayout();
            line-height: 1;
            margin: 3px 0 6px;
        }

        .qode-plc-rating {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            font-family: "ElegantIcons";
            font-size: 12px;
            line-height: 1.1em;
            letter-spacing: 6px;
            color: $first-main-color;
            overflow: hidden;
            -webkit-font-smoothing: antialiased;

            &:before {
                content: "\e031\e031\e031\e031\e031";
            }

            span {
                position: absolute;
                top: 0;
                left: 0;
                overflow: hidden;

                &:before {
                    content: "\e033\e033\e033\e033\e033";
                }
            }
        }

        .qode-plc-price {
            @include qodeTransform(none);
            color: #888;
            font-size: 13px;
            font-weight: 400;
            opacity: 1;
            @include qodeTransition(all 0.3s ease);
            will-change: transform;

            * {
                font-size: inherit;
                line-height: inherit;
            }

            del {
                margin: 0 5px 0 0;
            }

            ins {
                text-decoration: none;
            }
        }

        .qode-plc-add-to-cart{
            font-size: 11px;
            font-weight: 600;
            text-transform: uppercase;
            position: absolute;
            bottom: 0;
            left: 50%;
            @include qodeTransform(translateX(-50%));
            letter-spacing: 2px;
            opacity: 0;
            @include qodeTransition(opacity 0.3s ease);

            a,
            a.added_to_cart{
                color: #000;
                white-space: nowrap;
                background-color: transparent !important;

                &.qode-read-more-button{
                    position: relative;
                    display: block;
                }

                &.qode-add-to-cart:after,
                &.qode-read-more-button:after,
                &.qode-variable:after,
                &.qode-external:after,
                &.qode-grouped:after{
                    position: relative;
                    display: inline-block;
                    vertical-align: middle;
                    font-family: "ElegantIcons";
                    font-size: 13px;
                    text-transform: none;
                    line-height: 1em;
                    margin-left: 5px;
                    top: -2px;
                }

                &.qode-add-to-cart:after {
                    content: "\e013";
                }

                &.qode-read-more-button:after {
                    content: "\55";
                }

                &.qode-variable:after {
                    content: "\64";
                    top: -1px;
                }

                &.qode-external:after {
                    content: "\e02b";
                }
                &.qode-grouped:after {
                    content: "\55";
                }
            }

            .button,
            .added_to_cart {
                margin: 16px 0 0;
            }

            .added {
                display: none;
            }
        }

        .qode-plc-image {
            @include qodeRelativeHolderLayout();

            img {
                display: block;
                width: 100%;
            }

            .qode-plc-onsale,
            .qode-plc-out-of-stock,
            .qode-plc-new-product {
                position: absolute;
                display: block;
                top: 15px;
                right: 20px;
                width: auto;
                color: #000;
                font-size: 13px;
                font-weight: 600;
                line-height: 24px;
                letter-spacing: 1px;
                text-align: center;
                z-index: 100;
                @include qodeTransform(translateZ(0));
                box-sizing: border-box;
            }

            .qode-plc-new-product {
                right: auto;
                left: 20px;
            }
        }

        .qode-plc-text {
            position: absolute;
            display: inline-block;
            opacity: 0;
            box-sizing: border-box;
            @include qodeTransition(opacity 0.2s ease-in-out);
            white-space: nowrap;
            background-color: rgba(255, 255, 255, 0.95);
            @include qodeTransform(none);
            height: 100%;
            width: 100%;
            left: 0;
            top: 0;
            z-index: 8;
        }

        .qode-plc-text-outer {
            position: relative;
            display: table;
            width: 100%;
            height: 100%;
        }

        .qode-plc-text-inner {
            font-size: 0; //to remove space between inline-block items
            position: relative;
            display: table-cell;
            width: 100%;
            height: 100%;
            vertical-align: middle;
            text-align: center;
            font-size: unset;
            padding: 0 30px;

            &.qode-has-additional-info{
                padding-bottom: 50px;
            }
        }

        .qode-plc-link {
            position: absolute;
            top: 0;
            left: 0;
            display: inline-block;
            width: 100%;
            height: 100%;
            z-index: 9;
        }

        .qode-plc-additional-info{
            font-size: 0; //because of the space between items
            position: absolute;
            bottom: 6%;
            left: 50%;
            @include qodeTransform(translateX(-50%));
            z-index: 10;
        }

        &:hover{
            .qode-plc-text {
                @include qodeTransition(all .3s cubic-bezier(0, 0, 0.5, 1));
                opacity: 1;
            }

            .qode-plc-add-to-cart{
                opacity: 1;
            }
        }
    }

    .qode-owl-slider .owl-nav{
        .owl-prev {
            @include qodeTransform(translateX(-120%))
        }
        .owl-next{
            @include qodeTransform(translateX(120%))
        }
    }
}
/* ==========================================================================
   Product List - Carousel shortcode styles - end
   ========================================================================== */