/* ==========================================================================
   YITH WishList styles - begin
   ========================================================================== */

#yith-wcwl-popup-message {
    position: absolute; //absolute centering over added item
    margin:0 !important; //absolute centering over added item
    -webkit-transform-origin:0 0;
    transform-origin:0 0;
    @include qodeTransform(translate(-50%,-50%));
    border: 1px solid #ebebeb;
    border-radius: 0;
    padding: 0 21px;
    line-height: 35px;

    #yith-wcwl-message {
        font-size: 14px;
        font-weight: 600;
        letter-spacing: 0.1em;

        &:before {
            position: relative;
            font-family: 'ElegantIcons';
            content: "\4e";
            top: 2px;
            right: 7px;
        }
    }
 
    //vanishing effect
    &.qode-wishlist-vanish-out {
      @include qodeAnimation(qodeWishlistvanishOut 1s both);
    }

    @keyframes qodeWishlistvanishOut {
      0% {
        opacity: 1;
        @include qodeTransform(scale(1,1) translate(-50%,-50%));
        -webkit-filter: blur(0px);
        filter: blur(0px);
      }

      100% {
        opacity: 0;
        @include qodeTransform(scale(2,2) translate(-50%,-50%));
        -webkit-filter: blur(10px);
        filter: blur(10px);
      }
    }
}

.qode-yith-wcqv-holder {
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
}

.yith-wcwl-add-to-wishlist {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    margin: 0 3px;
    background-color: #000;
    width: 37px;
    height: 37px;
    line-height: 35px;
    text-align: center;
}

.yith-wcwl-add-button,
.yith-wcwl-wishlistaddedbrowse,
.yith-wcwl-wishlistexistsbrowse {

    a {
        position: relative;
        display: inline-block;
        vertical-align: top;
        font-size: 0;
        line-height: inherit;
        color: #fff;

        &:after {
            content: "\e089";
            position: relative;
            display: inline-block;
            vertical-align: top;
            font-family: 'ElegantIcons';
            font-size: 13px;
            line-height: inherit;
            -webkit-font-smoothing: antialiased;
            -moz-osx-font-smoothing: grayscale;
            @include qodeTransition(opacity 0.2s ease, color 0.2s ease);
        }

        // content: "\4e";

        &:not(.qode-adding-to-wishlist):hover {
            &:after {
                opacity: .8;
            }
        }

        &.qode-adding-to-wishlist {
            @include keyframes(qodeWishlistLoading) {
                0%, 100% {
                    opacity:1;
                }

                50% {
                    opacity: 0.2;
                }
            }

            &:after {
                @include qodeAnimation(qodeWishlistLoading 1s ease infinite);
            }
        }

        &:hover {
            color: #fff;
        }
    }
}

.yith-wcwl-wishlistaddedbrowse,
.yith-wcwl-wishlistexistsbrowse {

    a {
        &:after {
            content: "\4e";
        }
    }
}

.yith-wcwl-add-button {
    .ajax-loading {
        display: none !important;
    }
}

.yith-wcwl-wishlistaddedbrowse,
.yith-wcwl-wishlistexistsbrowse {

    span {
        display: none;
    }

    a {

        &:after {
            color: #fff;
        }
    }
}

.woocommerce-wishlist {

    .wishlist-title{
        display: none;
    }

    .woocommerce-message,
    .woocommerce-info,
    .woocommerce-error {
        position: relative;
        display: block;
        padding: 15px 30px;
        margin: 50px 0 30px;
        line-height: 48px;
        list-style: none;
        background-color: transparent;
        border: 1px solid #000;

        a {
            float: right;
        }
    }

    table.wishlist_table {
        font-size: inherit;
        margin: 50px 0 100px;

        thead {
            display: none;
        }

        tbody {

            tr {
                border: 0;
                border-bottom: 1px solid #000;

                td {
                    padding: 22px 0;
                    vertical-align: middle;
                    text-align: center;

                    &.product-remove {
                        border: 0;

                        a {
                            font-size: 0;

                            &:after {
                                content: "\f404";
                                display: inline-block;
                                vertical-align: middle;
                                speak: none;
                                color: inherit;
                                font-family: "Ionicons";
                                font-size: 25px;
                                font-style: normal;
                                font-weight: normal;
                                font-variant: normal;
                                text-transform: none;
                                text-rendering: auto;
                                line-height: 1;
                                -webkit-font-smoothing: antialiased;
                                -moz-osx-font-smoothing: grayscale;
                            }

                        }
                    }

                    &.product-thumbnail {
                        padding-left: 8px;
                        padding-right: 8px;
                        width: 92px;

                        a, img {
                            display: block;
                        }
                    }

                    &.product-name {
                        text-align: left;
                        padding-left: 20px;

                        a {
                            font-size: 14px;
                            font-weight: 600;
                            letter-spacing: 2px;
                            text-transform: uppercase;
                        }

                        .yith-wcqv-button {
                            height: 38px;
                            line-height: 38px;
                            padding: 0 28px;
                            margin: 0;
                        }
                    }

                    &.product-price{
                        font-size: 14px;

                        ins{
                            text-decoration: none;
                        }
                    }

                    &.product-stock-status span{
                        color: #000 !important;
                    }

                    &.product-add-to-cart {

                        a {
                            display: inline-block !important;
                            background-color: transparent;
                            border: 0;
                            padding: 0;
                            margin: 0;
                            font-size: 12px;
                            font-weight: 600;
                            text-transform: uppercase;
                            letter-spacing: 2px;
                            color: #000;
                            cursor: pointer;
                            line-height: 50px;
                            height: 50px;

                            &:hover{
                                background: transparent;
                                color: inherit;
                            }
                        }
                    }
                }
            }
        }

        .yith-wcwl-share{
            margin: 0;
        }
    }
}

.product .summary {
    .yith-wcwl-add-to-wishlist {
        width: auto;
        height: auto;
        background-color: transparent;
        display: inline-block;
        margin: 0 0 0 12px;

        .yith-wcwl-add-button,
        .yith-wcwl-wishlistaddedbrowse,
        .yith-wcwl-wishlistexistsbrowse {
            a {
                font-size: 11px;
                line-height: inherit;
                color: #fff;
                background-color: $first-main-color;
                border: 2px solid $first-main-color;
                border-radius: 0;
                box-sizing: border-box;
                text-transform: uppercase;
                letter-spacing: 1px;
                line-height: 34px;
                font-weight: 800;
                padding: 0 22px;
                @include qodeTransition(all 0.3s ease);

                &:after {
                    content: "\e089";
                    font-size: 12px;
                    float: left;
                    padding-right: 10px;
                    line-height: 33px;
                    color: inherit;
                    @include qodeTransition(none);
                }

                &:hover{
                    background-color: transparent;
                    color: $first-main-color;

                    &:after{
                        opacity: 1;
                    }
                }
            }
        }

        .yith-wcwl-add-button {
            a {
                &:after {
                    content: "\e030";
                }
            }
        }
    }
}

.product.product-type-grouped .summary{
    .yith-wcwl-add-to-wishlist {
        margin: -2px 0 15px 0;
    }
}

.qode-wishlist-widget-holder{
    display: inline-block;
    vertical-align: middle;

    a {
        color: $first-main-color;
        font-size: 13px;
        margin: 0 10px;

        .qode-wishlist-items-number {
            color: #000;
            font-size: 12px;
            font-weight: 600;
        }

        &:hover {
            color: #000;
        }
    }
}

header.light {
    .qode-wishlist-widget-holder{
        a {
            color: #fff;
            .qode-wishlist-items-number {
                color: #fff;
            }

            &:hover{
                opacity: 0.8;
            }
        }
    }
}

header.dark {
    .qode-wishlist-widget-holder{
        a {
            color: #000;
            .qode-wishlist-items-number {
                color: #000;
            }

            &:hover{
                opacity: 0.8;
            }
        }
    }
}

/* ==========================================================================
   YITH WishList styles - end
   ========================================================================== */