/* ==========================================================================
   YITH Quick View styles - begin
   ========================================================================== */
#yith-quick-view-modal,
.yith-quick-view.yith-modal {

    .yith-quick-view-overlay {
        background-color: rgba(#000, .7);
    }

    .yith-wcqv-wrapper {
        padding: 0;
        width: 1100px;
        max-width: 1100px;
        height: auto !important;
    }

    .yith-wcqv-main {
	    @include qodeRelativeHolderLayout();
	    padding: 0;
	    overflow: hidden !important;
	    box-shadow: none;
    }

    #yith-quick-view-content {
        .product{
            margin: 0;
        }

        .images,
        .summary {
            position: relative;
            float: left;
            width: 50%;
            box-sizing: border-box;
            margin: 0;
        }

        .images {

            a, img {
                display: block;
            }
        }

        .summary {
            padding: 46px 22px 50px 35px;

            .qode-yith-product-title {
                display: block;
                margin: 0 0 14px;
            }

            div[itemprop="offers"] {
                position: relative;
                display: block;
            }

            .price {
                position: relative;
                display: block;
                margin: 0 0 13px;
            }

            .woocommerce-product-rating {
		        @include qodeRelativeHolderLayout();
		        margin: 0;

                a.woocommerce-review-link{
                    display: inline-block;
                }

            }

            div[itemprop="description"],
            .woocommerce-product-details__short-description{
                @include qodeRelativeHolderLayout();
	            margin-bottom: 20px;

                p {
                    margin: 0;
                }
            }

            .qode-single-product-share {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                margin-top: 20px;
            }

        }
    }

    #yith-quick-view-close {
        top: 15px;
        right: 15px;
        width: 30px;
        height: 30px;
        font-size: 0;
        line-height: 30px;
	    font-weight: 400;
        text-align: center;
        opacity: 1;
        border: 0;
        color: #000;

        &:before {
            position: relative;
            display: inline-block;
            vertical-align: top;
            font-family: 'ElegantIcons';
            content: "\4d";
	        font-size: 30px;
            line-height: inherit;
            -webkit-font-smoothing: antialiased;
            -moz-osx-font-smoothing: grayscale;
        }

        &:hover {
            opacity: 0.8;
        }
    }
}
/* ==========================================================================
   YITH Quick View  styles - end
   ========================================================================== */