
.qode-elegant-product-list{
    .qode-onsale,
    .qode-out-of-stock,
    .qode-new-product {
        position: absolute;
        display: block;
        top: 15px;
        right: 20px;
        width: auto;
        color: #000;
        font-size: 13px;
        font-weight: 600;
        line-height: 24px;
        letter-spacing: 1px;
        text-align: center;
        z-index: 100;
        @include qodeTransform(translateZ(0));
        box-sizing: border-box;
    }
}
