ul.products {
    > .product{
        > .yith-wcqv-button{
            display: none !important;
        }
    }
}

%yith-wcqv-button-style {
    .qode-yith-wcqv-holder {
        display: inline-block;
        vertical-align: middle;
        height: 37px;
        margin: 0 3px;

        .yith-wcqv-button {
            position: relative;
            display: inline-block;
            background-color: #000;
            color: #fff;
            padding: 0px 30px;
            font-size: 11px;
            font-weight: 700;
            height: 37px;
            line-height: 37px;
            text-transform: uppercase;
            letter-spacing: 1px;


            div {
                background-size: 15px 15px !important;
            }
        }
    }
}

.qode-elegant-pl-holder .qode-pli-inner .qode-pli-text-inner,
.qode-plc-holder .qode-plc-item .qode-plc-text-inner,
ul.products > .product .qode-pl-inner .qode-pl-text-inner{
    @extend %yith-wcqv-button-style;
}