    position: absolute;
    display: inline-block;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    vertical-align: middle;
}

.woocommerce ul.products li.product .add-to-cart-button-inner{
    position: relative;
    display: table;
    width: 100%;
    height: 100%;
    vertical-align: middle;
}

.woocommerce ul.products li.product .add-to-cart-button-inner2{
    position: relative;
    display: table-cell;
    width: 100%;
    height: 100%;
