/* ==========================================================================
   WooCommerce Product List Elegant page styles - begin
   ========================================================================== */

.qode-elegant-pl-main-holder {
    @include qodeRelativeHolderLayout();
    margin: 0 0 22px;
}

.woocommerce ul.products.elegant {
    list-style: none;
    margin: 0 -10px;
    clear: both;

    > .product {
        position: relative;
        float: left;
        display: inline-block;
        vertical-align: middle;
        padding: 0 10px;
        margin: 0 0 20px;
        box-sizing: border-box;
        border: 0 !important;

        .touch & {
            cursor: pointer;
        }

        &:hover {
            .qode-pl-inner .qode-pl-text {
                opacity: 1;
            }
            .price {
                opacity: 0;
                @include qodeTransform(translateX(60px));
            }
            .qode-pl-add-to-cart{
                opacity: 1;
                @include qodeTransform(translateX(-50%));
                @include qodeTransition(all .4s .1s);
            }

            .qode-pl-add-to-cart{
                opacity: 1;
                @include qodeTransform(translateX(-50%));
                @include qodeTransition(all .4s .1s);
            }

            .qode-pl-text-inner {
                opacity: 1;
            }
        }

        .qode-onsale,
        .qode-out-of-stock,
        .qode-new-product {
            position: absolute;
            display: block;
            top: 15px;
            right: 20px;
            width: auto;
            color: #000;
            font-size: 13px;
            font-weight: 600;
            line-height: 24px;
            letter-spacing: 1px;
            text-align: center;
            z-index: 100;
            @include qodeTransform(translateZ(0));
            box-sizing: border-box;
        }

        .qode-new-product {
            right: auto;
            left: 20px;
        }

        .qode-product-list-category{
            margin: 0 0 4px;
            a{
                text-transform: uppercase;
                color: #888;
                font-size: 11px;
	            font-weight: 500;
                letter-spacing: 1px;
            }
        }

        .qode-product-list-title {
            margin: 0 0 8px;
            color: #000;
            letter-spacing: 0;
            text-transform: none;
            white-space: normal;
        }

        .qode-pl-text-inner {
            @include qodeTransition(all .3s ease-in-out);
            opacity: 0;
        }

        span.price {
            color: #888;
            font-size: 13px;
            opacity: 1;
            @include qodeTransition(all 0.3s ease);
            will-change: transform;
            line-height: 23px;
            letter-spacing: 0;
            font-style: normal;

            * {
                font-size: inherit;
                line-height: inherit;
            }

            del {
                margin: 0 5px 0 0;
            }

            ins {
                text-decoration: none;
            }
        }

        .qode-pl-add-to-cart{

            position: absolute;
            bottom: 0;
            left: 50%;
            @include qodeTransform(translateX(-100%));

            opacity: 0;
            @include qodeTransition(all 0.4s ease);

            a,
            a.added_to_cart{
                color: #000;
                white-space: nowrap;
                font-size: 11px;
                font-weight: 600;
                text-transform: uppercase;
                letter-spacing: 2px;
                border: 0;
                line-height: 1em;
                height: auto;
                background-color: transparent !important;

                &.qode-add-to-cart:after,
                &.qode-read-more-button:after,
                &.qode-variable:after,
                &.qode-external:after,
                &.qode-grouped:after{
                    position: relative;
                    display: inline-block;
                    vertical-align: middle;
                    font-family: "ElegantIcons";
                    font-size: 13px;
                    text-transform: none;
                    line-height: 1em;
                    margin-left: 5px;
                    top: -2px;
                }

                &.qode-add-to-cart:after {
                    content: "\e013";
                }

                &.qode-read-more-button:after {
                    content: "\55";
                }

                &.qode-variable:after {
                    content: "\64";
                    top: -1px;
                }

                &.qode-external:after {
                    content: "\e02b";
                }
                &.qode-grouped:after {
                    content: "\55";
                }

                &:hover{
                    background-color: transparent;
                    color: #999;
                }
            }
        }

        .button,
        .added_to_cart {
            margin: 16px 0 0;
        }

        .added {
            display: none;
        }

        .qode-pl-inner {
            @include qodeRelativeHolderLayout();
            overflow: hidden;

            .qode-pl-image {
                @include qodeRelativeHolderLayout();
                overflow: hidden;

                img {
                    display: block;
                    width: 100%;
                }
            }

            .qode-pl-text {
                position: absolute;
                bottom: 4%;
                left: 50%;
                @include qodeTransform(translateX(-50%));
                display: inline-block;
                opacity: 0;
                box-sizing: border-box;
                @include qodeTransition(opacity .2s ease-out, background-color .2s ease-out);
                z-index: 10;
                white-space: nowrap;
            }

            .qode-pl-text-inner {
                font-size: 0;

                .qode-product-list-title{
                    color: #fff;
                }

                .price {
                    color: #fff;

                    del {
                        color: #ccc;
                    }
                }
            }

            > a {
                @include qodeAbsoluteHolderLayout();
            }
        }

        .qode-pl-text-wrapper {
            @include qodeRelativeHolderLayout();
            margin: 22px 0 12px;
            text-align: center;
        }
    }
}


/* ==========================================================================
   WooCommerce Product List Elegant page styles - end
   ========================================================================== */